#confirmDeletion {
  width: 480px;
  height: 249px;
  position: absolute;
  padding: 48px;
  border-radius: 10px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: none;
}
#confirmDeletion #heading {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.5px;
}
#confirmDeletion #text {
  font-weight: 500;
  font-size: 12px;
  line-height: 22px;
  letter-spacing: -0.25px;
  margin: 13px 0 16px 0;
}
#confirmDeletion #con {
  display: flex;
  justify-content: flex-end;
  height: 48px;
  /* width: 100%; */
}
#confirmDeletion #cancel {
  width: 91px;
  border: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  border-radius: 24px;
}
#confirmDeletion #delete {
  width: 89px;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #ffffff;
  border-radius: 24px;
  background-color: #ec5757;
  border: 0;
  margin-left: 10px;
  height: 48px;
}
#confirmDeletion #delete:hover {
  opacity: 0.7;
}
#confirmDeletion #cancel:hover {
  background-color: #dfe3fa;
}

@media (max-width: 540px) {
  #confirmDeletion {
    width: 90%;
    height: auto;
  }
}
