#viewInvoiceStats {
  padding: 30px;
  border-radius: 10px;
}
#firstStats {
  display: flex;
  justify-content: space-between;
}
#userAddress {
  text-align: right;
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  letter-spacing: -0.229167px;
}
#description {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  margin-top: 8px;
}
#secondStats {
  margin-top: 21px;
  margin-bottom: 45px;
  display: flex;
  justify-content: space-between;
}
.statsHeading {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
.statsHeadingInfo {
  font-weight: bold;
  font-size: 15px;
  line-height: 20px;
  letter-spacing: -0.3125px;
  margin-top: 12px;
}
#ciientAddress {
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  letter-spacing: -0.229167px;
  margin-top: 8px;
}
#paymentDue {
  margin-top: 32px;
}

#thirdStats {
  padding: 32px;
  max-height: 203px;
  overflow: auto;
  border-radius: 10px;
}
.tableHeading {
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  letter-spacing: -0.229167px;
}
#fourthStats {
  height: 80px;
  display: flex;
  padding: 32px;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
}
#amountDue {
  font-weight: 500;
  font-size: 11px;
  line-height: 18px;
  letter-spacing: -0.229167px;
  color: #ffffff;
}
#amountDuePrice {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.5px;
  color: #ffffff;
}
#userStreet,
#clientStreet {
  width: 110px;
}

@media (max-width: 590px) {
  #firstStats {
    display: block;
  }
  #userAddress {
    margin-top: 30px;
    text-align: left;
  }
  #secondStats #clientInfo {
    margin: 20px 0;
  }
  #secondStats {
    display: block;
  }
}
