.invoice {
  display: flex;
  justify-content: space-around;
  background: white;
  width: 100%;
  height: 72px;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  cursor: pointer;
  position: relative;
}
.invoice:hover {
  border: 1px solid #7c5dfa;
  text-decoration: none;
}
.invoiceId {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
.invoiceIdFirst {
  color: #7e88c3;
}

.paymentDue {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
.clientName {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
.invoice h3 {
  font-weight: bold;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.8px;
  width: 91px;
}

.invoiceStatus {
  height: 40px;
  width: 129px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
}

@media (max-width: 602px) {
  .invoice {
    display: block;
    padding: 20px;
    height: auto;
  }
  .clientName {
    position: absolute;
    top: 20px;
    right: 20px;
  }
  .paymentDue {
    margin-top: 30px;
    margin-bottom: 10px;
  }
  .invoiceStatus {
    position: absolute;
    right: 20px;
    top: 70px;
  }
  .invoice-arrow-right {
    display: none;
  }
}
