#formElement {
  position: absolute;
  width: 616px;
  right: 0;
  display: none;
  padding: 0 30px 30px 30px;
  height: 914px;
  overflow: auto;
}
#invoiceHeading {
  font-weight: bold;
  font-size: 24px;
  line-height: 32px;
  letter-spacing: -0.5px;
  margin-top: 56px;
}
.partName {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #7c5dfa;
  margin-top: 48px;
  margin-bottom: 24px;
}
.formInputText {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #7e88c3;
  margin-bottom: 10px;
  display: inline-block;
}
#billFromAddress,
#billToAddress,
#calenderAndInvoice {
  display: flex;
  justify-content: space-between;
}
#billFromAddress .section1,
#billToAddress .section2 {
  width: 152px;
  height: 72px;
}
#formElement .input {
  width: 100%;
  height: 48px;
  padding-left: 20px;
  margin-bottom: 24px;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
#calenderAndInvoice input,
#calenderAndInvoice button {
  height: 48px;
  width: 240px;
}
#calenderAndInvoice .cmp {
  margin-top: 24px;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #0c0e16;
}

#calenderAndInvoice input {
  margin-bottom: 24px;
}
#calenderAndInvoice button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
#calenderAndInvoice .dropdown {
  /* remove this */
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #0c0e16;
}
#calenderAndInvoice .dropdown-menu {
  width: 100%;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
#calenderAndInvoice .dropdown-item {
  height: 48px;
  display: flex;
  align-items: center;
}
.thridDropdown {
  border-top: 1px solid gray;
  border-bottom: 1px solid gray;
}
.secondDropdown {
  border-top: 1px solid gray;
}
#formElement #itemHeading {
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  letter-spacing: -0.375px;
  color: #777f98;
  margin-top: 32px;
  margin-bottom: 16px;
}
#addNewItem {
  width: 100%;
  height: 48px;
  border: 0;
  border-radius: 10px;
  border: 2px solid #dfe3fa;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  letter-spacing: -0.25px;
}
#itemListHeading .itemName {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}

@media (max-width: 992px) {
  #formElement {
    top: 150px;
    width: 100%;
  }
}
@media (max-width: 540px) {
  #calenderAndInvoice {
    display: block;
  }
  #calenderAndInvoice button,
  #billToAddress .section2,
  #billFromAddress .section1 {
    width: 100%;
    margin-bottom: 24px;
  }
  .cmp .formInputText,
  #billToAddress,
  #billFromAddress {
    display: block;
  }
  #calenderAndInvoice #invoiceDate {
    width: 100%;
  }
}
