#mainPageHeading {
  display: flex;
  width: 730px;
  height: 59px;
  justify-content: space-between;
  margin: auto;
  /* this element is positioned relative to the home page */
  position: absolute;
  top: 72px;
  left: 50%;
  transform: translateX(-50%);
  /* we want this heading to have a lower stacking order so we can add new invoices */
  z-index: -1;
}
#mainPageHeading h1 {
  font-weight: bold;
  font-size: 32px;
  line-height: 36px;
  letter-spacing: -1px;
}
#mainPageHeading h3 {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}

#dropdownMenuButton {
  border-color: transparent;
  background-color: transparent;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  margin-right: 40px;
}
#mainPageOptions {
  display: flex;
  align-items: center;
}
#mainPageOptions .dropdown-menu.show {
  display: flex;
  left: -115px !important;
  top: 20px !important;
}
#newInvoice {
  width: 150px;
  height: 48px;
  background-color: #7c5dfa;
  border: 0;
  border-radius: 50px;
}
#plusContainer {
  width: 32px;
  height: 32px;
  background-color: white;
  display: inline-block;
  border-radius: 30px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin-right: 20px;
}
#plusContainer img {
  width: 10px;
  height: 10px;
}
#invoiceText {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #ffffff;
}
#mainPageOptions .dropdown-item:hover {
  background-color: transparent;
}

.dropdown-item input {
  margin-right: 15px;
  display: inline-block;
  cursor: pointer;
}
.dropdown-item input:hover {
  outline: 1px solid #7c5dfa;
}
.dropdown-item span {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
@media (max-width: 602px) {
  #mainPageOptions .dropdown-menu.show {
    display: block;
    top: 1px !important;
    left: 10px !important;
  }
  #plusContainer {
    display: none;
  }
}
