#sidebar {
  width: 103px;
  height: 850px;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  /* the sidebar is fixed to the screen */
  position: absolute;
  left: 0;
}
#sidebar #myImage {
  width: 40px;
  height: 40px;
  border-radius: 20px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
}
#sidebar #logoContainer {
  background: linear-gradient(to top, #9277ff, #7c5dfa);
  height: 103px;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
#sidebar #logo-image {
  width: 40px;
  height: 38px;
}
#sidebar .themeColor {
  cursor: pointer;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
#sidebar span {
  display: block;
  height: 113px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  /* inherits the parents width, which is sidebar */
  width: inherit;
}
#sidebar #myImageContainer {
  height: 50%;
  border-bottom: 1px solid #979797;
  /* inherits the parents width, which is span */
  width: inherit;
}
