#viewInvoiceStatus {
  height: 88px;
  margin-top: 32px;
  margin-bottom: 24px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 24px 32px;
}
#pending,
#paid,
#draft {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;
}

.status {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  margin-right: 20px;
}

.status-icon {
  height: 40px;
  width: 129px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
}
.stats {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
#pending .stats {
  color: #ff8f00;
}
#paid .stats {
  color: #33d69f;
}

.circle {
  width: 8px;
  height: 8px;
  display: inline - block;
  border-radius: 50px;
  margin-right: 8px;
}
#pending .circle {
  background: #ff8f00;
}
#paid .circle {
  background-color: #33d69f;
}

.first-con {
  display: flex;
  align-items: center;
}
.edit {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  width: 73px;
  height: 48px;
  border-radius: 24px;
  border: 0;
}
.delete {
  width: 89px;
  height: 48px;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #ffffff;
  border-radius: 24px;
  background-color: #ec5757;
  border: 0;
  margin-left: 10px;
}

.mark-as-paid,
.mark-as-pending {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
  color: #ffffff;
  border: 0;
  border-radius: 24px;
  width: 131px;
  height: 48px;
  background-color: #7c5dfa;
  margin-left: 10px;
}

#viewInvoiceStatus .delete:hover,
#viewInvoiceStatus .mark-as-paid:hover,
#viewInvoiceStatus .mark-as-pending:hover {
  opacity: 0.7;
}
#viewInvoiceStatus .edit:hover {
  background-color: #dfe3fa;
}

@media (max-width: 700px) {
  .second-con {
    position: absolute;
    bottom: -150px;
    background: white;
    width: 100%;
    left: 0;
    border-radius: 10px;
    height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
  }
}
