#homePage {
  position: relative;
}
#listOfInvoices {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 192px;
  z-index: -1;
  height: 664px;
  /* we want it to scorll */
  overflow: auto;
  width: 730px;
}
.sidebarContainer {
  width: 719px;
  background-color: white;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  position: relative;
}
#saveOrDeleteBtn {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-evenly;
  width: 619px;
  height: 110px;
  right: 0;
  align-items: center;
  display: none;
}
#cancelOrSave {
  position: absolute;
  bottom: 0;
  display: flex;
  justify-content: space-evenly;
  width: 619px;
  height: 110px;
  right: 0;
  align-items: center;
}
#saveOrDeleteBtn #discard,
#cancelOrSave #cancel {
  width: 96px;
  height: 48px;
  background: #f9fafe;
  color: #7e88c3;
  border-radius: 24px;
  border: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;

  letter-spacing: -0.25px;
}
#saveOrDeleteBtn #saveAsDraft {
  width: 133px;
  height: 48px;
  background-color: #373b53;
  border-radius: 24px;
  border: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
#saveOrDeleteBtn #saveAndSend,
#cancelOrSave #saveChanges {
  width: 133px;
  height: 48px;
  background-color: #7c5dfa;
  border-radius: 24px;
  color: white;
  border: 0;
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}
/* medium screen sizes and below */
@media (max-width: 992px) {
  #sidebar {
    transform: rotate(270deg);
    height: 100vw;
    transform-origin: 100% 0;
    margin-left: -100px;
    border-top-right-radius: unset;
    border-bottom-right-radius: unset;
  }
  .sidebarContainer {
    width: 100%;
  }
  #saveOrDeleteBtn {
    bottom: -150px;
    width: 100%;
  }
  #logoContainer,
  #sidebar .themeColor,
  #myImage {
    transform: rotate(90deg);
  }
  #listOfInvoices {
    top: 292px;
    width: 90%;
  }
  #mainPageHeading {
    top: 172px;
    width: 90%;
  }
}
/* small screen sizesand below */
@media (max-width: 602px) {
  #mainPageHeading {
    display: block;
  }
  #mainPageOptions {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}
