#viewInvoice {
  width: 730px;
  /* this element is positioned relative to the home page */
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 72px;
  z-index: -1;
}
#goback img {
  margin-right: 10px;
}
#goback span {
  font-weight: bold;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: -0.25px;
}

@media (max-width: 992px) {
  #viewInvoice {
    top: 172px;
  }
}
@media (max-width: 784px) {
  #viewInvoice {
    width: 90%;
  }
}
