#noInvoice {
  text-align: center;
}
#noInvoice div {
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  text-align: center;
  letter-spacing: -0.25px;
  width: 220px;
  margin: 0 auto;
}
#noInvoice h2 {
  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
  letter-spacing: -0.625px;
  width: 218px;
  margin: 64px auto 24px auto;
}

@media (max-width: 992px) {
  #noInvoice {
    margin-top: 50px;
  }
}
